import React, { PureComponent,Fragment} from 'react'
import IcInput from '@eui/ic-input'
import IcIcon from '@eui/ic-iconfont'
import Markdown from 'ic-markdown'
import largeInput from './large.md'
import defaultInput from './default.md'
import smallInput from './small.md'

export default class extends PureComponent {
    render() {
        return (
            <Fragment>
                <section className="single-icon">large输入框</section>
                <section>
                    <IcInput size="large" disabled={true}></IcInput>
                </section>
                <section>
                    <IcInput size="large"></IcInput>
                </section>
                <section>
                    <IcInput size="large" width="300px" addOnAfter={<IcIcon type="HR-interview"></IcIcon>}></IcInput>
                </section>
                <section>
                    <IcInput size="large" width="400px"></IcInput>
                </section>
                <section>
                    <Markdown>
                        {largeInput}
                    </Markdown>
                </section>
                <section>default输入框</section>
                <section>
                    <IcInput disabled={true}></IcInput>
                </section>
                <section>
                    <IcInput></IcInput>
                </section>
                <section>
                    <IcInput width="300px" addOnAfter={<IcIcon type="HR-interview"></IcIcon>}></IcInput>
                </section>
                <section>
                    <IcInput width="400px"></IcInput>
                </section>
                <section>
                    <Markdown>
                        {defaultInput}
                    </Markdown>
                </section>
                <section>small输入框</section>
                <section>
                    <IcInput size="small" disabled={true}></IcInput>
                </section>
                <section>
                    <IcInput size="small"></IcInput>
                </section>
                <section>
                    <IcInput size="small" width="300px" addOnAfter={<IcIcon type="HR-interview"></IcIcon>}></IcInput>
                </section>
                <section>
                    <IcInput size="small" width="400px"></IcInput>
                </section>
                <section>
                    <Markdown>
                        {smallInput}
                    </Markdown>
                </section>
            </Fragment>
        );
    }
}



