import React, {Fragment} from 'react'
import IcLoading from "@eui/ic-loading";
import normal from './normal.md'
import text from './text.md'
import global from './global.md'
import delay from './delay.md'
import Button from '@eui/ic-button'
import MarkDown from 'ic-markdown'
import './index.scss'
class Loading extends React.PureComponent {
    constructor(props) {
        super(props)
        this.state = {
            show: false
        }
    }
    componentWillUnmount() {
        if (this.timmer) {
            clearTimeout(this.timmer)
        }
    }
    onClick =()=> {
        this.setState({
            show: true
        })
        this.timmer = setTimeout(() => {
            this.setState({
                show: false
            })
        }, 2000);
    }
    render() {
        return (<Fragment>
            <section className='mb20'>
                <div>普通loading</div>
                <IcLoading/>
                <MarkDown>{normal}</MarkDown>
            </section>
            <section className='mb20'>
                <div>带辅助文字loading</div>
                <IcLoading text='加载中...' />
                <MarkDown>{text}</MarkDown>
            </section>
            <section className='mb20'>
                <div>delay属性的Loading</div>
                {this.state.show ? null : <IcLoading delay={2000} text='加载中...' />}
                <MarkDown>{delay}</MarkDown>
            </section>
            <section className='mb20'>
                <div>全局loading</div>
                <div style={{textAlign: 'center', marginBottom: '20px'}}>
                    <Button onClick={this.onClick}>弹出全局loading</Button>
                </div>
                {this.state.show ? <IcLoading type='global' text='加载中...' /> : null}
                <MarkDown>{global}</MarkDown>
            </section>
        </Fragment>)
    }
}


export default Loading


