module.exports = "## ic-tag\n\n\n描述：tag component for ifchange\n\n创建人：sophiehui\n\n创建时间：2018年05月02日 11:13\n\n仓库地址：http://gitlab.zhinanzhen.wiki/labs/hr-components\n\n-----------\n\n\n标签选择器分为两类，一类是关键词标签：标签的颜色（color：必须是16进制）；第二类是筛选标签：关闭属性boolean值（closable：false or true）；例子如下：\n\n````js\nimport IcTag from '@eui/ic-tag'\n\n<IcTag color = \"#ccc\">关键词标签</IcTag>       // 关键词标签\n\n<IcTag color = \"#11AABB\">我是超过七个字的标签</IcTag>       // 关键词标签\n\n<IcTag closable={true} onClose={()=>{}}>我是筛选标签</IcTag>    // 筛选标签\n\n````\n\n*Tips:筛选标签**不需要**传入color，关键词标签的的颜色必须使用**16进制**色值*\n\n### 参数描述\n\n参数名|类型|是否必须|说明|默认值\n---|----|---|-----|---\ncolor| string | 否 |标签的颜色（16进制）| #FFF\nclassName| string | 否 |自定义标签的class| -\nclosable| boolean | 否 |标签是否可关闭| false\nstyle| object | 否 |用来接收style定义的行内样式| -\nonClose| function | 否 |筛选标签的关闭回调函数| -\nbgColor| string | 否 |tag的背景颜色（16进制，不传的时候默认是参数color的颜色）| -\n"

