module.exports = "## ic-tooltip\n\n描述：气泡提示组件\n\n创建人：suzhen.han\n\n创建时间：2018年05月04日 11:39\n\n仓库地址：http://gitlab.zhinanzhen.wiki/labs/hr-components\n\n-----------\n\n当placement的值为`topLeft` `topRight` `bottomLeft` `bottomRight` `leftTop` `leftBottom` `rightTop` `rightBottom`时，默认气泡与目标元素左对齐或右对齐。\n但是当目标元素的宽高较小时将以下列情况渲染：\n- 当目标元素的width小于48px时，placement的值为`topLeft` `topRight` `bottomLeft` `bottomRight`时箭头指向目标元素的左右对称轴线；\n- 当目标元素的height小于48px时，placement的值为`leftTop` `leftBottom` `rightTop` `rightBottom`时箭头指向目标元素的上下对称轴线；\n\n### 参数描述\n\n| 参数名 | 类型  | 是否必须 | 说明  | 默认值 |\n| --- | --- | ---- | --- | --- |\n| title | string \\| ReactNode | 是 | 气泡中文字 |  |\n| placement | string | 否 | 气泡框位置，可选 `top` `left` `right` `bottom` `topLeft` `topRight` `bottomLeft` `bottomRight` `leftTop` `leftBottom` `rightTop` `rightBottom` | top  |\n| wrapperClass | string | 否 | 气泡容器类名 |  |\n| autoAdjustOverflow | boolean | 否 | 气泡被遮挡时自动调整位置 | true |\n| arrowPointAtCenter | boolean | 否 | 箭头是否指向目标元素中心 | false |\n| getTooltipContainer | Function | 否 | 浮层渲染父节点，默认渲染到body上 | () => document.body |\n| trigger | string | 否 | 触发行为，可选 hover/focus/click/contextMenu | hover |\n| mouseEnterDelay | number | 否 | 鼠标移入后延时多少才显示 Tooltip，单位：秒 | 0 |\n| mouseLeaveDelay | number | 否 | 鼠标移出后延时多少才隐藏 Tooltip，单位：秒 | 0.1 |\n\n-----\n\n### Blog:\n\n- 2018年05月04日 11:39：**suzhen.han** 创建组件\n"

